/*eslint-disable */
import React, {useState, useEffect, useContext} from 'react';
import {useHistory, useParams} from 'react-router-dom'; 
import './Detail.scss';
import {재고context} from './App.js';

 

function Detail(props){
    let {param} = useParams();
    let history = useHistory();
    let [alertYn, setAlertYn] = useState(true);
    let [inputVal, setInputVal] = useState('');
    let 재고2 = useContext(재고context);

    useEffect(()=>{
       let timer = setTimeout(()=>{
            setAlertYn(false);
        }, 2000);
        console.log('비체');
        return ()=>{ clearTimeout(timer)}
    }, []);

    let detailInfo = props.shoes.find((dd)=>{
       return dd.id == param
    });
    //방문기록
    return(
        <div className="container">
            <p className="red">Detail </p>
            {inputVal}
            <br/>
            <input onChange={(e)=>{setInputVal(e.target.value) }}/>
            {
                alertYn
                ?(<div className="my-alert_two">
                    <p>제고가 얼마 남지 않았습니다.</p>
                </div>)
                :null
            }
            
            <div className="row">
                <div className="col-md-6">
                <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                <h4 className="pt-5">{detailInfo.title}</h4>
                <p>{detailInfo.content}</p>
                <p>{detailInfo.price}원</p>
                
                <Info 재고={재고2}></Info>
                
                
                <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,10,11]) }}>주문하기</button>&nbsp;
                <button className="btn btn-danger" onClick={()=>{
                        history.goBack(); //history.push('/');
                }}>뒤로가기</button> 
                </div>
            </div>
        </div> 
    )
}

function Info(props){
    
    return(
        <p>재고 : {props.재고[0]}</p>
    )
}

export default Detail;